.movies {
    display: flex;
    width: 80%;
    padding: 70px 50px 50px;
    justify-content: space-between;
    flex-wrap: wrap;
    align-items: flex-start;
  }
  
  .movies .movie {
    display: flex;
    width: 45%;
    margin-bottom: 70px;
    padding: 20px;
    border-radius: 5px;
    background-color: #fff;
    font-weight: 300;
    color: #adaeb9;
    align-items: flex-start;
    justify-content: space-between;
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25), 0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.25);
  }
  
  .movie .container {
    display: flex;
    height: 100%;
    justify-content: center;
  }
  
  .movie img {
    width: 100%;
    max-width: 180px;
    margin-top: -50px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3), 0 -12px 36px -8px rgba(0, 0, 0, 0.25);
  }
  
  .movie .movie__title,
  .movie .movie__year {
    margin: 0;
    font-weight: 300;
  }
  
  .movie .movie__title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
  }
  
  .movies .movie__genres {
    display: flex;
    margin: 5px 0;
    padding: 0;
    list-style: none;
  }
  
  .movie__genres li {
    margin-right: 10px;
    font-size: 14px;
  }
  